.title {
	text-align: center;

	// .title__main
	&__main {
		color: #00222d;
		font-family: "RobotoSlab";
		line-height: calc(36 / 30);
		// @include resp-font(30, 20);
		font-size: 30px;

		@media (max-width: $md3+px) {
			font-size: 26px;
		}

		@media (max-width: $md4+px) {
			font-size: 20px;
		}
	}

	// .title__item
	&__item {
		margin: 30px 0px 0px 0px;
		font-weight: 700;
		// @include resp-font(22, 18);
		font-size: 22px;

		@media (max-width: $md4+px) {
			font-size: 18px;
		}
	}

	&_mxw {
		max-width: 450px;

		.title__main {
			text-align: left;
		}
	}
}

// =======================================
.slider-box {

	// .slider-box__image
	&__image {
		width: 100%;
		padding: 0px 0px 135% 0px;
		border: 10px solid #cce9e6;

	}

	// .slider-box__info
	&__info {
		font-size: 14px;
		background-color: #cce9e6;
		margin: -69px 0px 0px 0px;
		position: relative;
		z-index: 2;
		text-align: center;
		padding: 14px 0px;
		font-weight: 300;

		strong {
			font-weight: 700;
		}

		p {
			max-width: 180px;
			margin: 0 auto;
		}
	}

	@media (max-width: 519px) {
		width: 240px;
		margin: 0 auto;
	}
}


