//====================================================================================================

#back-top {
	position: fixed;
	bottom: 10%;
	z-index: 100;
	right: 20px;

	a {
		width: 50px;
		height: 50px;
		background-color: $decor-bg;
		border-radius: 50%;
		display: block;
		transition: 1s;
		opacity: .8;

		@media (min-width: 992px) {
			&:hover {
				opacity: 1;
				background-color: $decor-bg-hover;
			}
		}
	}

	span {
		width: 50px;
		height: 50px;
		display: block;
		background: url("../../dist/img/icons/up-arrow.png") center no-repeat;
		transition: 1s;
		opacity: 0.7;
	}
}


//====================================================================================================


.es {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

//====================================================================================================
.table {
	display: table;
	font-size: 0;
	width: 100%;
	//table-layout: fixed;
}

.trow {
	display: table-row;
}

.cell {
	display: table-cell;

	&.full {
		width: 100%;
	}
}

//====================================================================================================
.ibg {
	position: relative;
}

.ibg img {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	-o-object-fit: cover;
	object-fit: cover;
}

body.ie .ibg {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

body.ie .ibg img {
	width: 0;
	height: 0;
	opacity: 0;
	visibility: hidden;
}


//====================================================================================================
.graystyle {
	@include anim(0.8s, 0s);
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}

.graystyleoff {
	filter: grayscale(0);
	-webkit-filter: grayscale(0);
	-moz-filter: grayscale(0);
	-o-filter: grayscale(0);
}

//====================================================================================================
.slick-slider {
	position: relative;

	.slick-track,
	.slick-list {
		-webkit-transform: translate3d(0, 0, 0);
		-moz-transform: translate3d(0, 0, 0);
		-ms-transform: translate3d(0, 0, 0);
		-o-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
	}

	.slick-list {
		position: relative;
		overflow: hidden;
		width: 100%;

	}

	.slick-track {
		position: relative;
		width: 100%;
		display: flex;
		// display: block;
		align-items: stretch;
	}

	.slick-slide {
		overflow: hidden;
		float: left;
		position: relative;

	}

	.slick-dots {
		li {
			button {}

			&.slick-active {
				button {}
			}
		}
	}
}

//=====================================================================================================================
.tab__item {
	display: none;

	&.active {
		display: flex;
	}
}

