.gm-style {}

.gm-style-iw-t {
	opacity: 0;
}

.baloon {
	opacity: 1;
	right: -7px !important;
	bottom: 80px !important;

	button {
		display: none !important;
	}

	&:after {
		display: none !important;
	}
}

.baloon-style {
	display: none;
}

.baloon-content.gm-style-iw {
	opacity: 1;
	border-radius: 0px !important;
	max-width: 300px !important;
	padding: 0 !important;
	left: 0 !important;
	width: 100% !important;
	overflow: visible !important;

	>.gm-style-iw-d {
		overflow: hidden !important;
		max-width: none !important;
	}

	&:after {
		display: none !important;
	}

}

.baloon-close {
	top: 18px !important;
}

